import React, { useEffect, useState } from "react";
import "./Popout.css"
import "./AssigningTaskPopout.css"
import axios from "axios";
import fetchMultipleProfilePic from '../components/FetchMultipleProfilePic';

function AssigningTaskPopout ({onClose, taskid, creator, onRefresh}) {
    const [refresh,setRefresh] = useState(false)
    const [currentSection, setCurrentSection] = useState("addPeople") //addPeople or assign
    const username = sessionStorage.getItem('username')
    const [friendList, setFriendList] = useState([])
    const [taskParticipants, setTaskParticipants] = useState([])
    const [notTaskParticipants, setNotTaskParticipants] = useState([])
    const [taskAssignedPpl, setTaskAssignedPpl] = useState([])
    const [notTaskAssignedPpl, setNotTaskAssignedPpl] = useState([])
    const [taskAssignedtoYOU, setTaskAssignedtoYOU] = useState(false)

    //FETCHING DATA=======================================================
    useEffect( ()=> {

        //getting all the friends
        const gettingAllFriends = async (username)=> {
            const response = await axios.post('https://public-distask-backend.onrender.com/TaskAssigning', {action:"getting all friends", username})
            setFriendList( response.data.friends);
        }
        gettingAllFriends(username);

        //getting participants from this specific task
        const getParticipantsFromTask =  async (taskid)=> {
            const response = await axios.post('https://public-distask-backend.onrender.com/TaskAssigning', {action:"get Participants from Task", taskid})
            setTaskParticipants(response.data.participants)
        }
        getParticipantsFromTask(taskid);

        //getting ppl thats assigned to this task
        const getPplAssignedToTask = async (taskid) => {
            const response = await axios.post('https://public-distask-backend.onrender.com/TaskAssigning', {action:"get Ppl assigned from Task", taskid})
            setTaskAssignedPpl(response.data.usersAssigned)
        }
        getPplAssignedToTask(taskid)

    },[refresh])

    //data processing refresh
    useEffect(()=> {
        setNotTaskParticipants(friendList.filter(friend => !taskParticipants.includes(friend)) )
        setNotTaskAssignedPpl(taskParticipants.filter(friend => !taskAssignedPpl.includes(friend)) )

        setTaskAssignedtoYOU(taskAssignedPpl.includes(username))
        console.log("task assigned to you:" + taskAssignedtoYOU)
        onRefresh();
    },[friendList, taskParticipants, refresh, taskAssignedPpl])





    //HANDLER=============================================================
    const handleAddPeopleInTask = async (participant) => {
        const response = await axios.post('https://public-distask-backend.onrender.com/TaskAssigning', {action:"adding participants into task", participant, taskid})
        if(response.status===200){
            setRefresh(!refresh)
        }
    }

    const handleRemovePeopleInTask = async (participant)=> {
        const response = await axios.post('https://public-distask-backend.onrender.com/TaskAssigning', {action:"remove participants from task", participant, taskid})
        if(response.status===200){
            setRefresh(!refresh)
        }
    }


    const handleAssignPeopleInTask = async(participant)=> {
        const response = await axios.post('https://public-distask-backend.onrender.com/TaskAssigning', {action:"assign people in task", participant, taskid})
        if(response.status===200){
            setRefresh(!refresh)
        }
    }

    const handleRemoveAssignPeopleInTask = async(participant)=> {
        const response = await axios.post('https://public-distask-backend.onrender.com/TaskAssigning', {action:"Remove assigned people in task", participant, taskid})
        if(response.status===200){
            setRefresh(!refresh)
        }
    }


    // for putting profile picture
    const [taskParticipantsProfilePics,setTaskParicipantsProfilePics] = useState({})
    const [notTaskParticipantsProfilePics, setNotTaskParticipantsProfilePics] = useState({})
    const [taskAssignedPplProfilePics, settaskAssignedPplProfilePics] = useState({})
    const [notTaskAssignedPplProfilePic, setnotTaskAssignedPplProfilePics] = useState({})
    const [AllParticiapntsProfilePic, setALlParticipantsProfilePic] = useState({})
    useEffect(()=>{
        const fetchProfilePics = async () => {
            // //for task participants
            // if (taskParticipants.length > 0) {
            //   const pics = await fetchMultipleProfilePic(taskParticipants);
            //   setTaskParicipantsProfilePics(pics);
            // } else {
            //     setTaskParicipantsProfilePics({});
            // }

            // // for notTaskParticipants
            // if (notTaskParticipants.length > 0) {
            //     const pics = await fetchMultipleProfilePic(notTaskParticipants);
            //     setNotTaskParticipantsProfilePics(pics);
            // } else {
            //     setNotTaskParticipantsProfilePics({});
            // }
            
            // // for taskAssignedPpl
            // if (taskAssignedPpl.length > 0) {
            //     const pics = await fetchMultipleProfilePic(taskAssignedPpl);
            //     settaskAssignedPplProfilePics(pics);
            // } else {
            //     settaskAssignedPplProfilePics({});
            // }

            // // for notTaskAssignedPpl
            // if (notTaskAssignedPpl.length > 0) {
            //     const pics = await fetchMultipleProfilePic(notTaskAssignedPpl);
            //     setnotTaskAssignedPplProfilePics(pics);
            // } else {
            //     setnotTaskAssignedPplProfilePics({});
            // }



            const allParticipants = [
                ...new Set([
                    ...taskParticipants,
                    ...notTaskParticipants,
                    ...taskAssignedPpl,
                    ...notTaskAssignedPpl
                ])
            ];

            if (allParticipants.length > 0){
                const pics = await fetchMultipleProfilePic(allParticipants);
                setALlParticipantsProfilePic(pics)
            } else {
                setALlParticipantsProfilePic({})
            }
    





        };
          fetchProfilePics();
    },[taskParticipants, notTaskParticipants, currentSection, taskAssignedPpl, notTaskAssignedPpl])


    useEffect(()=>{

    },[])




    return(
        <div className="popout">

            {/* top layer */}
            <div className="top-layer">
                <p className="wording">Task Assigning</p>
                <button onClick={onClose} className="close">Close</button>
            </div>

            {/* Assigning Task */}
            
            <div className="topsection">
                <button className={`sectiontabbutton ${currentSection === "addPeople" ? "active" : "inactive"}`} onClick={()=>setCurrentSection("addPeople")}>
                    Add People
                </button>
                <button className={`sectiontabbutton ${currentSection === "assign" ? "active" : "inactive"}`} onClick={()=>setCurrentSection("assign")}>
                    Assigning Task
                </button>
            </div>

            <div>
            {  /* { ADDING PEOPLE HERE VVVVVVVVVVVVVVVVVVVVVVVVVVVVVV */
                (currentSection==="addPeople") && (
                    <div className="whole_list">
                        <strong>Users Added in this Task</strong>
                        <ul className="participant-list">
                            {taskParticipants.map((participant, index) => (
                                <li key={index} className="participant-item">
                                <div className="participant-info">
                                    {AllParticiapntsProfilePic[participant] && AllParticiapntsProfilePic[participant]!==null && (
                                        <img src={`data:image/jpeg;base64,${AllParticiapntsProfilePic[participant]}`} alt={`${participant}'s profile`} className='profilePic' />
                                    )}
                                    <span className="participant-name">{participant}</span>
                                    <div className="action-container">
                                    {participant === creator 
                                        ? (<div className="action-container">
                                        <button className="creator-button" disabled>
                                          Creator (cannot be removed)
                                        </button>
                                      </div>)
                                        :  participant===username
                                        ? (<button onClick={() => handleRemovePeopleInTask(participant)} className="action-button">
                                        Leave
                                        </button>)
                                        : friendList.includes(participant) 
                                        ? (<button onClick={() => handleRemovePeopleInTask(participant)} className="action-button">
                                        Remove
                                        </button>)
                                        : (<div className="action-container">
                                            <button className="creator-button" disabled>
                                              Not in friendlist (cannot be removed)
                                            </button>
                                          </div>)
                                    }
                                    </div>
                                </div>
                                </li>
                            ))}
                        </ul>


                        



                        <strong>Add users</strong>
                        <ul className="participant-list">
                            {notTaskParticipants.map((participant, index) => (
                                <li key={index} className="participant-item">
                                <div className="participant-info">
                                    {AllParticiapntsProfilePic[participant] && AllParticiapntsProfilePic[participant]!==null && (
                                        <img src={`data:image/jpeg;base64,${AllParticiapntsProfilePic[participant]}`} alt={`${participant}'s profile`} className='profilePic' />
                                    )}
                                    <span className="participant-name">{participant}</span>
                                    <div className="action-container">
                                    <button onClick={() => handleAddPeopleInTask(participant)} className="action-button">
                                        Add
                                    </button>
                                    </div>
                                </div>
                                </li>
                            ))}
                        </ul>
                    
                    </div>
                )
            }

            {  /* ASSIGNING PEOPLE HERE VVVVVVVVVVVVVVVVVVVVVVVVVVVVV    */
                (currentSection==="assign") && (
                    <div className="whole_list">
                        <strong>Users Assigned</strong>
                        <ul className="participant-list">
                            {taskAssignedPpl.map((participant,index) => (
                                <li key={index} className="participant-item">
                                    <div className="participant-info">
                                        {AllParticiapntsProfilePic[participant] && AllParticiapntsProfilePic[participant]!==null && (
                                            <img src={`data:image/jpeg;base64,${AllParticiapntsProfilePic[participant]}`} alt={`${participant}'s profile`} className='profilePic' />
                                        )}
                                        <span className="participant-name">{participant}</span>
                                        <div className="action-container">
                                            <button onClick={()=>handleRemoveAssignPeopleInTask(participant)} className="action-button">
                                                Unassign
                                            </button>
                                        </div>
                                    </div>
                                </li>
                            ))}
                        </ul>



                        <strong>Users Unassigned</strong>     
                        <ul className="participant-list">
                            {notTaskAssignedPpl.map((participant,index) => (
                                <li key={index} className="participant-item">
                                    <div className="participant-info">
                                        {AllParticiapntsProfilePic[participant] && AllParticiapntsProfilePic[participant]!==null && (
                                            <img src={`data:image/jpeg;base64,${AllParticiapntsProfilePic[participant]}`} alt={`${participant}'s profile`} className='profilePic' />
                                        )}
                                        <span className="participant-name">{participant}</span>
                                        <div className="action-container">
                                            <button onClick={()=> handleAssignPeopleInTask(participant)} className="action-button">
                                                Assign
                                            </button>
                                        </div>
                                    </div>
                                </li>
                            ))}
                        </ul>

                        


                    </div>
                )
            }
            </div>


        </div>
    )
}

export default AssigningTaskPopout;